/* ==========================================================================
   Design System
   Expandable Styling
   ========================================================================== */
.o-expandable {
  position: relative;
}
.o-expandable__header {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  padding: 0.625em 0.9375em;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
.o-expandable__header:focus {
  outline: 1px dotted var(--black);
  outline-offset: 1px;
}
.o-expandable__header .o-expandable__cue-close,
.o-expandable__header .o-expandable__cue-open {
  display: none;
}
.o-expandable__header[aria-expanded='false'] .o-expandable__cue-open {
  display: block;
}
.o-expandable__header[aria-expanded='true'] .o-expandable__cue-close {
  display: block;
}
button.o-expandable__header {
  width: 100%;
  text-align: left;
}
.o-expandable__label {
  flex-grow: 1;
  margin-bottom: 0;
  color: var(--black);
  font-weight: 500;
}
.o-expandable__icon,
.o-expandable__label {
  font-size: 1.125em;
}
@media only all and (max-width: 37.5em) {
  .o-expandable__icon,
  .o-expandable__label {
    font-size: 1em;
  }
}
.o-expandable__cues {
  align-self: center;
  color: var(--pacific);
  font-size: 1em;
  line-height: 1.375;
}
.o-expandable__content {
  padding: 0.9375em;
  padding-top: 0;
}
.o-expandable__content::before {
  content: '';
  display: block;
  border-top: 1px solid var(--gray-40);
  padding-top: 0.9375em;
}
.o-expandable__content::after {
  padding-bottom: 0.9375em;
  width: 100%;
}
.o-expandable--padded .o-expandable__header {
  padding: 1.5625em 0.9375em;
}
.o-expandable--padded .o-expandable__icon,
.o-expandable--padded .o-expandable__label {
  font-size: 1.625em;
}
@media only all and (max-width: 37.5em) {
  .o-expandable--padded .o-expandable__icon,
  .o-expandable--padded .o-expandable__label {
    font-size: 1.375em;
  }
}
.o-expandable--background {
  background: var(--gray-5);
}
.o-expandable--border {
  border: 1px solid var(--gray-40);
}
.o-expandable-group .o-expandable {
  border-bottom: 1px solid var(--gray-40);
}
.o-expandable-group .o-expandable:first-child {
  border-top: 1px solid var(--gray-40);
}
@media print {
  .o-expandable__header[aria-expanded='true'] .o-expandable__cue-close,
  .o-expandable__header[aria-expanded='false'] .o-expandable__cue-open {
    display: none;
  }
  .o-expandable__content[aria-expanded='false'] {
    display: block;
    max-height: 99999px !important;
  }
}
.print .o-expandable__header[aria-expanded='true'] .print .o-expandable__cue-close,
.print .o-expandable__header[aria-expanded='false'] .print .o-expandable__cue-open {
  display: none;
}
.print .o-expandable__content[aria-expanded='false'] {
  display: block;
  max-height: 99999px !important;
}
html[lang='ar'] .o-expandable__header {
  text-align: right;
}
html[lang='ar'] .o-expandable__cues {
  text-align: left;
}
.o-summary__content {
  overflow-y: hidden;
  padding: 2px;
  left: -2px;
  top: -2px;
  position: relative;
}
.o-summary__btn {
  position: relative;
  z-index: 2;
  display: block;
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  border: dotted var(--pacific);
  border-width: 1px 0;
  text-align: center;
  color: var(--pacific);
  background: #fff;
}
.no-js .o-summary__btn {
  display: none;
}
.o-summary__btn:focus {
  outline: 1px dotted var(--pacific);
  outline-offset: 2px;
}
.o-summary__btn[aria-expanded='false']::before {
  display: block;
  pointer-events: none;
  height: 2.75em;
  margin: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: calc(-100% + 4px);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
  content: '';
}
@media print {
  .o-summary__btn[aria-expanded='false']::before {
    background: none;
  }
}
.print .o-summary__btn[aria-expanded='false']::before {
  background: none;
}
@media only screen and (min-width: 601px) {
  .o-summary--mobile .o-summary__btn {
    display: none;
  }
}
.o-summary-minimal__content {
  overflow-y: hidden;
  padding: 2px;
  left: -2px;
  top: -2px;
  position: relative;
}
.o-summary-minimal__btn {
  margin-top: 5px;
  padding-left: 0;
  padding-right: 0;
  display: block;
  border: none;
  background: none;
  text-align: left;
  color: var(--pacific);
}
.no-js .o-summary-minimal__btn {
  display: none;
}
.o-summary-minimal__btn:focus {
  outline: 1px dotted var(--pacific);
  outline-offset: 2px;
}
.o-summary-minimal__btn .o-summary-minimal__cue-close,
.o-summary-minimal__btn .o-summary-minimal__cue-open {
  display: none;
}
.o-summary-minimal__btn[aria-expanded='false'] .o-summary-minimal__cue-open {
  display: block;
}
.o-summary-minimal__btn[aria-expanded='true'] .o-summary-minimal__cue-close {
  display: block;
}
